.banner {
  background-color: #e16163;
  padding-top: 8% !important;
  margin-bottom: 5% !important;
}

.banner__img {
  width: 80%;
  position: relative;
  /*  top: 50% !important; */
}

.banner__text {
  align-self: center;
  padding-bottom: 10%;
}

.banner__text_title_bold {
  font-size: 1.563rem;
  line-height: 30px;
  text-align: left;
  font-weight: bold;
  color: white !important;
}
.banner__text_title_normal {
  font-size: 1.563rem;
  line-height: 32px;
  text-align: left;
  color: white !important;
}

.banner__text_subtitle {
