.information {
  background: #cbe776;
}

.information__title {
  font-size: 24px;
  line-height: 30px;
  color: #363736;
  font-weight: bold;
}

.information__subtitle {
  color: #363736;
  line-height: 18px;
  font-size: 15px;
  font-weight: 400;
}

.item_ods {
  background: #55a3eb !important;
  margin-bottom: 2em !important;
  border: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  color: white;
  text-align: center;
}

.list-group-item.active {
  background: #cbe776 !important;
}

/* Slides */
.slide-title {
  color: black;
  font-weight: bold;
}

.slide-text {
  color: black;
}

.slide-text-side {
  justify-content: center;
  flex-direction: column;
  display: flex;
}

.carousel-control-next {
  right: 0;
  color: black;
}

.carousel-control-next-icon {
  background-image: url("../../assets/icons/arrow-right-solid.svg") !important;
}

.carousel-control-prev-icon {
  background-image: url("../../assets/icons/arrow-left-solid.svg") !important;
}

@media only screen and (max-width: 600px) {
  .carousel-item {
    height: 100vh !important;
  }
}

.scrolling-wrapper-flexbox {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
.tarjeta {
  flex: 0 0 auto;
  margin-left: 5px;
  margin-right: 15px;
}

.tarjeta {
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 0.2s ease-in-out;
  background: #ffffff;
  margin-top: 20px;
  border-radius: 20px;
  width: 200px;
  height: 220px;
  text-align: center;
  padding: 0px 20px;
  box-shadow: 3px 4px 10px rgba(0, 0, 0, 0.3);
}
.tarjeta:hover {
  transform: scale(1.1);
  background-color: #55a3eb;
}

.scrolling-wrapper,
.scrolling-wrapper-flexbox {
  margin-top: 40px;
  height: 320px;
  margin-bottom: 0px;
  width: 100%;
  -webkit-overflow-scrolling: touch;
}

.timeline {
  background: #cbe776;
  scrollbar-width: thin;
}

.tarjeta-title {
  padding-top: 10px;
  font-size: 1.063em;
}

.tarjeta-text {
  font-size: 0.813em;
}

.timeline ::-webkit-scrollbar {
  /* width: 10px; */
  width: 2em;
  height: 0.75em;
  /* width: 1em; */
  margin: 0;
}

/* Track */
.timeline ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px transparent;
  border-radius: 10px;
  margin: 0;
}

/* Handle */
.timeline ::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
  margin: 0;
}

/* Handle on hover */
.timeline ::-webkit-scrollbar-thumb:hover {
  background: gray;
  margin: 0;
}

.card-ministerio {
  background-color: transparent;
  text-align: center;
}

.como-se-elabora {
  font-weight: bold;
  color: #363736 !important;
  font-size: 1.563rem;
  line-height: 56px;
  text-align: center;
}

.banner_presupuesto_total {
  background-color: #55a3eb;
}

.grow {
  transition: all 0.2s ease-in-out;
}
.grow:hover {
  transform: scale(1.1);
}

@media only screen and (max-width: 768px) {
  .banner__text_title {
    font-size: 1.25em;
  }
  .banner__text {
    padding-top: 2.5%;
  }
  .information__title {
    padding-top: 20px;
  }

  .banner__text_title_bold {
    font-size: 1.063rem;
    line-height: auto;
    text-align: left;
    font-weight: bold;
    color: white !important;
  }

  .banner__text {
    padding-bottom: 2.5%;
  }

  .banner__text_title_normal {
    font-size: 0.963rem;
    line-height: auto;
    text-align: left;
    color: white !important;
  }
}
